@import '../../../variables';

.error {
  &-image {
    width: 300px;
  }

  a {
    font-weight: bold;
    color: $main-color;
  }

  @media (min-width: 900px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;

    &-text {
      display: flex;
      align-items: center;
      width: 630px;
      border: 2px solid black;
      border-radius: 10px;
      background-color: rgba($color: #e7e7e7, $alpha: 0.5);
      padding: 0.5rem;
      box-shadow: 4px 4px 0 0 rgba($color: #000000, $alpha: 0.2);
    }

    &-paragraph {
      text-align: justify;
    }
  }
}
